.reviews__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 40px;
  padding: 120px 0;
}

.reviews__header {

}

.reviews__header h2 {
  margin: 0;
  color: $color-default-blue;
  font-size: 50px;
  font-style: normal;
  font-weight: 400;
  line-height: 50px;

  @include vp-1199 {
    font-size: 34px;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 20px;
  }
}

.reviews__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}


.reviews__controls {
  flex-basis: 100%;
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 52px;

  @include vp-767 {
    flex-basis: auto;
    gap: 0;
    order: 2;
  }
}

.reviews__pagination-container {
  position: relative;
  align-self: center;

  @include vp-767 {
    display: none;
  }
}

.reviews__pagination-container .swiper-pagination-progressbar {
  position: absolute;
  background-color: $color-default-blue-opacity20;
}

.reviews__pagination .swiper-pagination-progressbar-fill {
  background-color: $color-default-blue;
}

.reviews__buttons {
  align-self: center;
  justify-self: end;
}
