.btn-secondary {
  position: relative;

  //СИНИЙ КРУГ
  &::before {
    content: "";
    position: absolute;
    transform: translateY(-50%);
    right: 0;
    width: 30px;
    height: 30px;
    background-color: $color-default-blue;
    border-radius: 50%;
  }
}

.btn-secondary__arrow {
  position: absolute;
  top: -5px;
  right: 10px;
  display: inline-flex;
  width: 8px;
  height: 8px;
  border-top: 1px solid $color-default-white;
  border-right: 1px solid $color-default-white;
  transform: rotate(0deg);

  &::before {
    content: "";
    position: absolute;
    top: 3px;
    right: -2px;
    width: 12px;
    height: 1px;
    background-color: $color-default-white;
    transform: rotate(135deg);
  }
}
